    body: "\r\nمهمترین یافته‌ها:\r\n\r\nبرآورد مشارکت ۵۴ درصدی در انتخابات\r\n\r\nحدود نیمی از کسانی که قصد دارند در انتخابات شرکت کنند، مایلند رویکرد دولت سیزدهم ادامه پیدا‌ کند. \r\nمعترضان به عملکرد دولت، هنوز تمایلی به مشارکت در انتخابات ندارند.\r\n\r\nدر بین کسانی که قصد شرکت در انتخابات دارند، بیشترین گرایش به محمدباقر قالیباف بوده است.\r\nمیزان بالای آرای سرگردان در این انتخابات قابل توجه است و ۴۰ درصد از مشارکت‌کنندگان گزینه نهایی خود را انتخاب نکرده‌اند.\r\n\r\nنخستین نظرسنجی ملی شناخت در تاریخ ۲ تا ۴ خردادماه به صورت تلفنی و با حجم نمونه ۱۰۰۰ نفر برگزار شده است. شایان ذکر است که با نزدیک شدن به انتخابات، احتمال تغییر در برآوردهای اولیه وجود دارد.\r\n\r\nhttps://s30.picofile.com/file/8476280418/1.png\r\nhttps://s30.picofile.com/file/8476280350/2.png\r\nhttps://s30.picofile.com/file/8476280384/3.png\r\n\r\n\r\n\r\n",
