    body: " نتایج پیمایش انتخاباتی شناخت که ۵ و ۶ تیرماه انجام شده است نشان می‌دهد میزان مشارکت در انتخابات ریاست‌جمهوری با سه درصد حاشیه خطا حدود ۵۴ درصد برآورد می‌شود. \r\n\r\nدر میان شرکت کنندگان قطعی در انتخابات، مسعود پزشکیان ۳۰ درصد آرا را به خود اختصاص داده است.  پس از آن سعید جلیلی با ۲۱ درصد و محمدباقر قالیباف با ۲۰ درصد در رده‌های دوم‌ و سوم‌ قرار می‌گیرند. با در نظر گرفتن حاشیه خطا می‌توان گفت فاصله بین آقایان قالیباف و جلیلی معنادار نیست و نمی‌توان پیش‌بینی کرد کدامیک به دور دوم خواهند رفت.\r\n\r\nپیش‌بینی‌ می‌شود با فرض اینکه مشارکت به صورت ناگهانی افزایش پیدا نکند، انتخابات به دور دوم خواهد رفت.\r\n\r\nبا افزایش میزان مشارکت در انتخابات، شانس مسعود پزشکیان برای افزایش سبد رأی بیشتر خواهد بود.\r\n\r\nhttps://uploadkon.ir/uploads/b32527_24Slide-101-8T8.png\r\nhttps://uploadkon.ir/uploads/61ab27_24Slide-102-8T8.png\r\nhttps://uploadkon.ir/uploads/d54e27_24Slide-103-8T8.png\r\nhttps://uploadkon.ir/uploads/f06927_24Slide-104-8T8.png\r\nhttps://uploadkon.ir/uploads/298827_24Slide-105-8T8.png\r\n\r\n",
