    title: " تأثیر حمایت ظریف از نامزدها",
    slug: "-تأثیر-حمایت-ظریف-از-نامزدها1718800259585",
    body: "حمایت محمدجواد ظریف از مسعود پزشکیان یکی از نقاط خبرساز رقابت‌های انتخاباتی در روز اخیر بود. پرسش‌های مختلفی در خصوص تأثیر این حمایت بر پایگاه رأی مسعود پزشکیان در انتخابات وجود دارد.\r\n\r\nنتایج پیمایش انتخاباتی شناخت در تاریخ ۸ الی ۱۰ خردادماه نشان می‌دهد که حمایت ظریف می‌تواند در رأی بخشی از افرادی که قصد مشارکت در انتخابات دارند، اثرگذار باشد. ۹ درصد از پاسخگویان ابراز کرده‌اند در صورت حمایت ظریف از یک نامزد حتماً به او رأی خواهند داد و ۱۰ درصد نیز به احتمال زیاد به وی رأی خواهند داد. همچنین ۳۴ درصد از افراد نیز اعلام کرده‌اند رأی‌شان به نامزد مورد تأیید آقای ظریف مشروط است. \r\nاز سوی دیگر این نتایج نشان می‌دهد که حمایت ظریف برای ۲۶ درصد از جامعه رأی‌دهنده دارای اثر منفی است.\r\nبه طور کلی می‌توان گفت حمایت آقای ظریف این ظرفیت را دارد که ۱۹ درصد از آرا را به راحتی و ۳۴ درصد را به صورت مشروط به نفع یک نامزد جذب کند. میزان بالفعل شدن این ظرفیت در روزهای آتی مشاهده خواهد شد.\r\n\r\nhttps://uploadkon.ir/uploads/9fa919_24Slide-88-2-8T4-TK.png",
